<template>
    <el-container style=" border: 1px solid #eee">
        <el-header class="el-icon-s-home" style=" font-size: 40px; background-color:#409EFF; color:azure">
            教考系统</el-header>
        <el-footer>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1"><router-link to="/teacher" tag="span">账号信息</router-link></el-menu-item>
                <el-menu-item index="2"><router-link to="/questionBank" tag="span">题库管理</router-link></el-menu-item>
                <el-menu-item index="3"><router-link to="/examPaper" tag="span">试卷管理</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/examSchedule" tag="span">考试安排</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/examGrading" tag="span">阅卷</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/class" tag="span">班级管理</router-link></el-menu-item>
            </el-menu>
        </el-footer>


    <el-main>
      <div>
        <!-- 模块和题目 -->
    <el-card v-for="(module, index) in examPaper.modules" :key="index" class="module-card">
      <h4>模块 {{ module.moduleNum }} - 题型: {{ getExerciseTypeName(module.exerciseType) }}</h4>
      <el-table :data="module.moduleExercises" style="width: 100%">
        <el-table-column prop="exerciseInModuleNum" label="题号" width="80"></el-table-column>
        <el-table-column prop="exercise.exerciseInfo" label="题目描述"></el-table-column>
        <el-table-column prop="exercise.exerciseProblem" label="题目内容"></el-table-column>
        <el-table-column prop="exercise.exerciseAnswer" label="正确答案"></el-table-column>
      </el-table>
    </el-card>
  </div>
      
    </el-main>
    </el-container>
</template>

<script>
import axios from 'axios';
export default {
    //传递过来的试卷编号
    props: {id: Number},
    data() {

        return {
            formLabelWidth: '120px',
            exerciseTypes: [
        { typeNum: 1, typeName: '选择题' },
        { typeNum: 2, typeName: '填空题' },
        { typeNum: 3, typeName: '简答题' },
        ],
            examPaper: null,
        }
    },
    methods: {
        handleClick(row) {
            console.log(row);
        },
        onSubmit() {
            console.log('submit!');
        },
        getExerciseTypeName(typeNum) {
            const type = this.exerciseTypes.find(t => t.typeNum === typeNum);
            return type ? type.typeName : '未知题型';
        },
        //查询试卷详情
        getAll() {
            axios.get(`/api/exam/id/${this.id}`).then((result) => {
                if (result.data.code == 1&&result.data.data!=null) {
                    this.examPaper = result.data.data;
                    this.open2();
                }
                else {
                    this.open3();
                }

            }).catch((error) => {
                console.error('Error fetching exam data:', error);
                this.$message({
                    message: '获取试卷数据失败，请稍后再试。',
                    type: 'error'
                });
            });
        },
        //返回上一页
        goBack() {
            this.$router.back(); // 返回上一页
        },
        //删除数据
        deleteById(id) {
            axios.delete(`/api/exam/${id}`).then((result) => {
                if (result.code == 1) {
                    this.open2();
                    this.tableData = this.tableData.filter(item => item.id !== id);

                }
                else {
                    this.open3();
                }


            });

        },
        //添加试卷
        insertExam() {
            axios.post("/api/paper", this.form2).then((result) => {
                if (result.code == 1) {

                    this.open2();

                }
                else {
                    this.open3();
                }

            });

        },
        //重置
        reset() {
            this.form = '';
            this.dialogFormVisible = false
        },
        //成功提示
        open2() {
            this.$message({
                message: '恭喜你，这是一条成功消息',
                type: 'success'
            });
        },
        //错误提示
        open3() {
            this.$message({
                message: '警告哦，这是一条警告消息',
                type: 'warning'
            });
        },

    },
    mounted(){
        this.getAll();
    }
}
</script>


<style scoped>

.el-container {
    min-height: 100vh; /* 至少占据整个视口高度 */
    max-height: 1200px; /* 最大高度限制 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    display: flex;
    flex-direction: column;
  }
  
  .el-header, .el-footer {
    flex: 0 0 auto; /* 固定高度 */
    background-color: #409EFF;
    color: azure;
    padding: 10px;
  }
  
  .el-main {
    flex: 1 1 auto; /* 自动占据剩余空间 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    padding: 20px;
    box-sizing: border-box;
  }
  
  .exam-detail {
    width: 100%; /* 设置宽度为父元素的100% */
    padding: 10px;
    box-sizing: border-box;
  }
  
  .box-card {
    max-height: 1200px; /* 设置最大高度 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    padding: 20px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
  }
  
  .text.item {
    flex: 1; /* 每个题目占据相等的空间 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ddd; /* 可选：增加边框以区分题目 */
    box-sizing: border-box;
  }
  
  /* 响应式设计 */
  @media (max-width: 768px) {
    .el-container {
      min-height: auto; /* 小屏幕上高度自适应 */
      max-height: 800px; /* 小屏幕上的最大高度 */
    }
  }
  
  @media (min-width: 768px) {
    .el-container {
      min-height: 100vh; /* 中等屏幕上的最小高度 */
      max-height: 1200px; /* 中等屏幕上的最大高度 */
    }
  }
  
  @media (min-width: 1200px) {
    .el-container {
      min-height: 100vh; /* 大屏幕上的最小高度 */
      max-height: 1500px; /* 大屏幕上的最大高度 */
    }
  }
  </style>